.product{
    display:flex; 
    flex-direction: column;
    background-color:white;
    justify-content: flex-end;
    margin: 10px;
    padding:10px;
    width:100%;
    max-height:400px; 
    min-width:100px; 
     align-items: center;
     z-index: 1;
  
     
}
.product img{
    height:240px;
    width:200px;
    object-fit:contain;
    margin-top: 15px;
    margin-bottom: 15px;
    
}
.product__info{
    height:100px;
    margin-bottom: 10px;
    width:100%;
} 
.product__info p{
    margin-bottom:2px;
   
}
.product button{
    background-color: #f0c14b;
    border:1px solid;
    margin-top:5px;
    border-color: #a88734;
    color:#111
}
.product__price{
    margin-top: 5px;
}
.product__info p{
    margin-top:5px;
    margin-bottom:10px
}
.product__rating{
    display: flex;
}