.header{
    height:60px;
    display: flex;
    align-items: center;
    background-color: #131921;    
    position:sticky;
    top:0;
    z-index:100; 
  

}
.header__logo{
    width:100px;
    object-fit: contain;
    margin:0 20px;
    margin-top:12px;
   
}
.header__search{
    display: flex;
    flex:1;
    align-items: center;
    border-radius: 24px;
    padding:10px;
}
.header__searchInput{
    height:12px;
    padding:10px;
    border:none;
    width:100%;
}

.header__searchIcon{
    padding:5px;
    height:22px !important  ;
    background-color: #cd9042;
}
.header__optionLineOne{
    font-size:10px;
}
.header__optionLineTwo{
font-size:13px;
font-weight:800;
}


.header__nav{
    display: flex !important;
    justify-content: space-evenly !important;
  
}
.header_optionBasket{
    color:white;
    }

.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
    
}

.header__option{
    display: flex;
    flex-direction: column;
    color:white;
    margin-left: 10px;
    margin-right: 10px;

}